import React from 'react';
import { CurrencyService } from 'services/currency.service';

export function PriceButton(props) {
  const { price, disable, onClick, priceSelect } = props;
  return (
    <div className="col-sm-6" key={price}>
      <button
        style={{
          backgroundColor: '#e9ecef',
          width: '100%',
          height: '80%',
          borderRadius: '4px',
          border:
            priceSelect === price.value
              ? '1px solid #00c0ef'
              : '1px solid #ccc',
          color: priceSelect === price.value ? '#00c0ef' : 'black',
        }}
        value={price.value}
        className="btn btn-lg recharge"
        disabled={disable}
        onClick={() => onClick(price)}
        type="button"
      >
        {CurrencyService.formatCurrency(price.value)}
      </button>
    </div>
  );
}
