/* eslint-disable react/style-prop-object */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */

/**
 * Tekmedi Input Component
 * @param props extends StandardProps<
    React.HTMLAttributes<HTMLDivElement>,
    InputBaseClassKey,
    
     * `onChange`, `onKeyUp`, `onKeyDown`, `onBlur`, `onFocus` are applied to the inner `InputComponent`,
     * which by default is an input or textarea. Since these handlers differ from the
     * ones inherited by `React.HTMLAttributes<HTMLDivElement>` we need to omit them.
    
     'children' | 'onChange' | 'onKeyUp' | 'onKeyDown' | 'onBlur' | 'onFocus' | 'defaultValue'
    
  * @returns JSX.HTMLInputElement<InputProps>
  * @example <TekBaseInput
              type="text"
              placeholder="Nhập mã đặt chỗ"
              ref={register({ required: true })}
              inputClass="form-control"
              wrapClass="col-md-2"
              label="Mã đặt chỗ/ Mã hẹn"
              value={filterSearchPatient.filterCode}
              name="checkinCode"
              errors={errors}
              errorMessage="Nhập mã đặt chỗ"
              onChange={e => e}
            />
*/

import React from 'react';
import styled from 'styled-components';
import { InputBaseProps } from '@material-ui/core';

const defaultInputClass: string = 'form-group';
const Required: string = 'required';
const errorStyle = { color: ' #fb0606' };

export interface InputProps extends InputBaseProps {
  value: any;
  name: string;
  type: string;
  label?: string;
  style?: any;
  inputClass?: string | '';
  wrapClass?: string | '';
  errors?: any;
  errorMessage?: string | 'required';
  onClick?: any;
}

const InputWrapper = styled.input.attrs((props: InputProps) => ({...props}))`
  display: block;
  width: 100%;
  height: 39px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  outline: 0 !important;
  box-shadow: none !important;
`;

const TekBaseInput = React.forwardRef<HTMLInputElement, InputProps>((
  { 
    onChange, 
    name, 
    type, 
    inputClass, 
    placeholder, 
    label, 
    wrapClass, 
    value, 
    errorMessage, 
    errors,
    onClick
  },
  ref
): JSX.Element => {
  return (
    <div className={defaultInputClass + ' ' + wrapClass }>
      <label>{ label }</label>
      <InputWrapper
        onClick={() => onClick!()}
        value={value}
        onChange={onChange} 
        type={type} 
        className={inputClass}
        placeholder={value ? value : placeholder}
        name={name}
        ref={ref}
      />
      {errors && errors[name] && errors[name]?.type === Required && (
        <span style={errorStyle}>{ errorMessage }</span>
      )}
    </div>
  );
});

export default TekBaseInput;
