/* eslint-disable @typescript-eslint/no-unused-vars */
import { createSlice } from 'store/core/@reduxjs/toolkit';
import { RESPONSE_CONSTANT } from '../constants/card.constant';
import { AppHelper } from 'utils/app.helper';
import { RESPONSE_MESSAGE } from '../constants/card.constant';
export interface CardState {
  dataCardHistory?: any;
  dataCardStatistical?: any;
  dataPrinterHistory?: any;
  dataValueExtend?: any;
  loading?: boolean;
  error?: any;
  cardInfo?: any;
  linkHistory: any;
  success?: any;
  filePdf: any;
  filePdfOver: any;
  filePdfCardFee: any;
  filePdfRegister: any;
  filePdfRecharged: any;
  filePdfReturn: any;
}

export const initialState: CardState = {
  dataCardHistory: {},
  dataCardStatistical: {},
  dataPrinterHistory: {},
  dataValueExtend: {},
  loading: false,
  error: null,
  cardInfo: {},
  linkHistory: {},
  success: {},
  filePdf: {},
  filePdfOver: {},
  filePdfCardFee: {},
  filePdfRegister: {},
  filePdfRecharged: {},
  filePdfReturn: {},
};

const CardFormSlice = createSlice({
  name: 'card',
  initialState,
  reducers: {
    clear() {
      return initialState;
    },

    getDataCardHistory(state, action: any) {
      state.loading = true;
      state.error = null;
    },
    getDataCardHistorySuccess(state, action: any) {
      state.loading = false;
      state.dataCardHistory = action.payload;
    },
    getDataCardHistoryFailure(state, action: any) {
      state.error = action.payload;
      state.loading = false;
    },
    exportListCard(state, action) {
      state.loading = true;
    },
    exportListCardSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_CARD_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.EXPORT_CARD_SUCCESS,
      };
    },
    exportListCardFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    registerCardPayment(state, action) {
      state.loading = true;
    },
    registerCardPaymentSuccess(state, action) {
      state.loading = false;
    },
    registerCardPaymentFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    cancelPayment(state, action) {
      state.loading = true;
    },
    cancelPaymentSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.CANCEL_PAYMENT_SUCCESS,
        message: RESPONSE_MESSAGE.CANCEL_PAYMENT_SUCCESS,
      };
    },
    cancelPaymentFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.CANCEL_PAYMENT_FAIL,
        message: action.payload || RESPONSE_MESSAGE.CANCEL_PAYMENT_FAIL,
      };
    },

    getIdPrinter(state, action) {
      state.loading = true;
      state.error = null;
    },
    getIdPrinterSuccess(state, action) {
      state.loading = false;
      state.dataPrinterHistory = action.payload;
    },
    getIdPrinterFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    postCancelDeal(state, action) {
      state.loading = true;
      state.error = null;
    },
    postCancelDealSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.CANCEL_DEAL_SUCCESS,
        message: RESPONSE_MESSAGE.CANCEL_DEAL_SUCCESS,
      };
    },
    postCancelDealFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    getListValueExtend(state) {
      state.loading = true;
      state.error = null;
    },
    getListValueExtendSuccess(state, action) {
      state.loading = false;
      state.dataValueExtend = action.payload;
    },
    getListValueExtendFail(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    getCardStatistical(state, action) {
      state.loading = true;
    },
    getCardStatisticalSuccess(state, action) {
      state.loading = false;
      state.dataCardStatistical = action.payload;
    },
    getCardStatisticalFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },
    exportHistoryCard(state, action) {
      state.loading = true;
    },
    exportHistoryCardSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_FAIL,
        message: action.payload,
      };
    },
    exportHistoryCardPdf(state, action) {
      state.loading = true;
    },
    exportHistoryCardPdfSuccess(state, action) {
      state.loading = false;
      state.filePdf = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_PDF_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardPdfFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_PDF_FAIL,
        message: action.payload,
      };
    },
    exportHistoryCardPdfOver(state, action) {
      state.loading = true;
    },
    exportHistoryCardPdfOverSuccess(state, action) {
      state.loading = false;
      state.filePdfOver = action.payload.overview;
      state.filePdfCardFee = action.payload.card_fee;
      state.filePdfRegister = action.payload.register;
      state.filePdfRecharged = action.payload.recharged;
      state.filePdfReturn = action.payload.return;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_PDF_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.EXPORT_HISTORY_CARD_SUCCESS,
      };
    },
    exportHistoryCardPdfOverFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_HISTORY_CARD_PDF_FAIL,
        message: action.payload,
      };
    },
    setSuccess(state, action) {
      state.success = action.payload;
    },
    setError(state, action) {
      state.error = action.payload;
    },
    clearSuccess(state) {
      state.success = {};
    },
    clearError(state) {
      state.error = null;
    },
    clearDataPrint(state) {
      state.dataPrinterHistory = {};
    },
    clearFilePdf(state) {
      state.filePdf = {};
    },
    clearFilePdfOver(state) {
      state.filePdfOver = {};
      state.filePdfCardFee = {};
      state.filePdfRegister = {};
      state.filePdfRecharged = {};
      state.filePdfReturn = {};
    },

    exportTransactionCard(state, action) {
      state.loading = true;
    },
    exportTransactionCardSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_TRANSACTION_CARD_SUCCESS,
        data: action.payload,
        message: RESPONSE_MESSAGE.EXPORT_TRANSACTION_CARD_SUCCESS,
      };
    },
    exportTransactionCardFail(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.EXPORT_TRANSACTION_CARD_FAIL,
        message: action.payload,
      };
    },
  },
});

export const { actions, reducer, name: sliceKey } = CardFormSlice;
