/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import DateFnsUtils from '@date-io/date-fns';
import { Tooltip } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import AppLoading from 'app/components/Loading';
import { ModalConfirm } from 'app/components/Modal/Confirm';
import TekMultiSelect from 'app/components/MultiSelect';
import STableBody from 'app/components/STableBody';
import STableHead from 'app/components/STableHead';
import STableHeaderSort from 'app/components/STableHeaderSort';
import TableCellColor from 'app/components/TableCellColor';
import PrintReturnHistory from 'app/containers/CardPage/components/PrintReturnHistory';
import * as _ from 'lodash';
import { useSnackbar } from 'notistack';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useReactToPrint } from 'react-to-print';
import { Unsubscribe } from 'redux';
import { CurrencyService } from 'services/currency.service';
import { LocalStorageService } from 'services/localStorage.service';
import { NotifyService } from 'services/notify.service';
import * as fromAuth from 'store/auth/shared/selectors';
import * as CardConst from 'store/card/constants/card.constant';
import {
  cartTypesList,
  columnsHistoryCard,
  RESPONSE_CONSTANT,
} from 'store/card/constants/card.constant';
import { CardHttp } from 'store/card/services/card.http';
import { CardModel } from 'store/card/shared/Model';
import { CardFormSaga } from 'store/card/shared/saga';
import * as fromCardSelectors from 'store/card/shared/selectors';
import { actions, reducer, sliceKey } from 'store/card/shared/slice';
import {
  DEFAULT_FORMAT_DATE_TIME,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { RootStore } from 'store/configureStore';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { AppHelper, Order } from 'utils/app.helper';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  table: {
    width: '100%',
    minWidth: '1600px',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  red: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: 'red',
  },
  Green: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#00CD00',
  },
  Blue: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#3399ff',
  },
  SkyBlue: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#87CEEB',
  },
  Orchid: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#DA70D6',
  },
  Purple4: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#7D26CD',
  },
  Yellow: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#F09C42',
  },
  GreenYellow: {
    fontSize: '1.1rem',
    fontFamily: 'Roboto, sans-serif',
    color: '#FF34B3',
  },
});

export function HistoryPage() {
  const local = new LocalStorageService();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const cardHttpService = new CardHttp();
  const dateUtil = new DateFnsUtils();
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: CardFormSaga });
  const classes = useStyles();
  const loading: any = useSelector<any>(fromCardSelectors.selectLoading);
  const dataCardHistory = useSelector(fromCardSelectors.selectDataCardHistory);
  const user: any = useSelector<any>(fromAuth.selectUser);
  const dispatch = useDispatch();
  const [patientCode, setPatientCode] = useState('');
  const [patientName, setPatientName] = useState('');
  const [selectedTypes, setSelectedTypes] = useState<any[]>([]);
  const success: any = useSelector(fromCardSelectors.selectSuccess);
  const error: any = useSelector(fromCardSelectors.selectError);
  const [isOpenConfirm, setIsOpenConfirm] = useState<boolean>(false);
  const [idSelect, setIdSelect] = useState<string>('');
  let printRef = useRef<HTMLDivElement>(null);
  const [openPrintReturn, setOpenPrintReturn] = useState<boolean>(false);
  const [cardInfo, setCardInfo] = useState<any>();
  const [isOpenDisplay, setIsOpenDisplay] = useState(false);
  const [isCloseTable, setIsCloseTable] = useState(false);
  const reactToPrintContent = React.useCallback(() => {
    return printRef.current;
  }, [printRef.current]);

  const [order, setOrder] = React.useState<Order>('desc');
  const [orderBy, setOrderBy] = React.useState<any>('time');
  const orderRef = useRef<Order>('asc');
  const orderByRef = useRef<string>('time');
  useEffect(() => {
    orderRef.current = order;
    orderByRef.current = orderBy;
  }, [order, orderBy]);

  const handlePrint = useReactToPrint!({
    content: reactToPrintContent,
    removeAfterPrint: true,
  });

  const [filterCard, setFilterCard] = useState<any>({
    page: 0,
    rowsPerPage: _.first(rowsPerArray),
    patientCode: '',
    patientName: '',
    types: [],
  });

  const [submitedSearch, setSubmitedSearch] = useState<boolean>(false);
  useEffect(() => {
    if (!submitedSearch) return;
    if (!_.isEmpty(user)) {
      dispatch(
        actions.getDataCardHistory({
          ...filterCard,
          employeeId: !_.isEmpty(user) ? user.id : '',
          employeeName: !_.isEmpty(user) ? user.full_name : '',
          sort_field: orderByRef.current,
          sort_type: orderRef.current,
        }),
      );
    }
  }, [submitedSearch, user, filterCard, order, orderBy]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      if (success.key === RESPONSE_CONSTANT.CANCEL_PAYMENT_SUCCESS) {
        dispatch(
          actions.getDataCardHistory({
            ...filterCard,
            employeeId: !_.isEmpty(user) ? user.id : '',
            employeeName: !_.isEmpty(user) ? user.full_name : '',
            sort_field: orderRef.current,
            sort_type: orderByRef.current,
          }),
        );
      }
      dispatch(actions.clearSuccess());
    }
  }, [success]);

  useEffect(() => {
    if (!_.isEmpty(error)) {
      notifyController
        .setNotifyMessage(
          error?.message || CardConst.RESPONSE_MESSAGE.CANCEL_PAYMENT_FAIL,
        )
        .setFailNotify();
      dispatch(actions.clearError());
    }
  }, [error]);

  const memoCard: any = useMemo((): any => {
    if (!_.isEmpty(dataCardHistory)) {
      const { data = [], recordsTotal } = dataCardHistory;
      return { data, recordsTotal };
    }
  }, [dataCardHistory]);

  const closeConfirm = () => setIsOpenConfirm(false);
  const openCancelPayment = (id: any) => {
    setIsOpenConfirm(true);
    setIdSelect(id);
  };

  const cancelPayment = () => {
    if (idSelect === '') return;
    dispatch(
      actions.cancelPayment({
        history_id: idSelect,
        employee_id: user?.id,
      }),
    );
  };
  const handleOpenPrintReturn = id => {
    cardHttpService
      .getCardHistoryById(id)
      .then(response => response.data)
      .then(cardInfo => {
        if (AppHelper.checkResponseData(cardInfo)) {
          setCardInfo(cardInfo.result);
          setOpenPrintReturn(true);
          handlePrint!();
        } else {
          dispatch(
            actions.setError({
              id: AppHelper.generateUUID() + Date.now(),
              key: CardConst.RESPONSE_CONSTANT.NOT_FOUND_WITH_ID,
              message: cardInfo.message,
            }),
          );
        }
      })
      .catch(error => {
        console.error(error);
      });
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const renderColumns = () => {
    return (
      <STableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={columnsHistoryCard}
      />
    );
  };

  const renderTable = () => {
    if (validateCardList()) {
      const tableData: any[] = memoCard.data;
      return tableData.map((row, idx) => {
        const card = new CardModel(row);
        return (
          <TableRow key={row.id}>
            {columnsHistoryCard.map(column => {
              const value = card[column.id];
              if (column.id === 'birthday') {
                return (
                  <TableCell key={column.id}>
                    {value ? `${new Date(value).getFullYear()}` : '-'}
                  </TableCell>
                );
              } else if (column.id === 'time') {
                return (
                  <TableCell key={column.id}>
                    {value
                      ? dateUtil.format(
                          new Date(value),
                          DEFAULT_FORMAT_DATE_TIME,
                        )
                      : ''}
                  </TableCell>
                );
              } else if (
                column.id === 'price' ||
                column.id === 'before_value' ||
                column.id === 'after_value'
              ) {
                return (
                  <TableCell key={column.id} align="right">
                    {CurrencyService.formatCurrency(value)}
                  </TableCell>
                );
              } else if (column.id === 'actions') {
                switch (card.type) {
                  case 2:
                    return (
                      <TableCell key={column.id}>
                        <Tooltip title={'In'} enterDelay={300} leaveDelay={100}>
                          <button
                            className="btn btn-primary btn-xs m-0"
                            onClick={() => handleOpenPrintReturn(card.id)}
                          >
                            <i className="fa fa-print"></i>
                          </button>
                        </Tooltip>
                        {user?.role?.code === 'ADMIN' && (
                          <Tooltip
                            title={'Hủy'}
                            enterDelay={300}
                            leaveDelay={100}
                          >
                            <button
                              id="btnCancel"
                              className="btn btn-danger btn-xs ml-2 m-0"
                              onClick={() => openCancelPayment(card.id)}
                            >
                              <i className="fa fa-eraser"></i>
                            </button>
                          </Tooltip>
                        )}
                      </TableCell>
                    );
                  case 3:
                    return (
                      <TableCell key={column.id}>
                        <Tooltip title={'In'} enterDelay={300} leaveDelay={100}>
                          <button
                            className="btn btn-primary btn-xs"
                            onClick={() => handleOpenPrintReturn(card.id)}
                          >
                            <i className="fa fa-print"></i>
                          </button>
                        </Tooltip>
                      </TableCell>
                    );
                  case 11:
                    return (
                      <TableCell key={column.id}>
                        <Tooltip title={'In'} enterDelay={300} leaveDelay={100}>
                          <button
                            className="btn btn-primary btn-xs"
                            onClick={() => handleOpenPrintReturn(card.id)}
                          >
                            <i className="fa fa-print"></i>
                          </button>
                        </Tooltip>
                      </TableCell>
                    );
                  default:
                    return <TableCell key={column.id}>-</TableCell>;
                }
              } else if (column.id === 'manipulation') {
                return (
                  <TableCellColor
                    id={column.id}
                    key={column.id}
                    value={
                      card.type === 14 && value === ''
                        ? 'Nạp tiền hỗ trợ thanh toán'
                        : card.type === 15 && value === ''
                        ? 'Hủy tiền hỗ trợ thanh toán'
                        : value
                        ? value
                        : ''
                    }
                  />
                );
              }
              return (
                <TableCell key={column.id}>{value ? value : ''}</TableCell>
              );
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100 text-center"
            colSpan={columnsHistoryCard.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, filterCard, { page });
    setNewFilterCard(changePage);
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, filterCard, {
      rowsPerPage: +event.target.value,
      page: 0,
    });
    setNewFilterCard(changeRowPerPage);
  };

  const setNewFilterCard = newFilter => {
    setFilterCard(newFilter);
  };

  const submitSearch = e => {
    e.preventDefault();
    let types: number[] = [];
    if (selectedTypes.length) {
      types = selectedTypes.map(({ value }) => value);
    }
    const newFilter = {
      ...filterCard,
      types,
      patientCode,
      patientName,
      page: 0,
    };
    setFilterCard(newFilter);
    setSubmitedSearch(true);
  };

  const changePatientCode = e => setPatientCode(e.target.value);

  const changePatientName = e => setPatientName(e.target.value);

  const validateCardList = () =>
    dataCardHistory.data && dataCardHistory.data.length ? true : false;

  const renderPagination = () => {
    if (validateCardList()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={memoCard.recordsTotal}
          rowsPerPage={filterCard.rowsPerPage}
          page={filterCard.page}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };
  const getOptionLabel = option => `${option.label}`;
  const handleToggleOption = selectedOptions =>
    setSelectedTypes(selectedOptions);
  const handleClearOptions = () => setSelectedTypes([]);
  const handleSelectAll = isSelected => {
    if (isSelected) {
      setSelectedTypes(cartTypesList);
    } else {
      handleClearOptions();
    }
  };

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case actions.exportTransactionCardSuccess.type:
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
      dispatch(actions.clear());
    };
  }, []);

  return (
    <div className="row" style={{ display: isCloseTable ? 'none' : 'block' }}>
      <div className="col-md-12">
        <div className="tabbable-line">
          <ul className="nav customtab nav-tabs" role="tablist"></ul>
          <div className="tab-content">
            <div className="tab-pane active fontawesome-demo" id="tab1">
              <div className="row">
                <div className="col-md-12">
                  <div className="card card-topline-red">
                    <div className="card-head">
                      <header>Lịch sử thẻ</header>
                    </div>
                    <div
                      className="card-body"
                      style={{ display: isOpenDisplay ? 'none' : 'block' }}
                    >
                      <form onSubmit={submitSearch}>
                        <div className="row">
                          <div className="col-md-2 pr-0">
                            <input
                              type="text"
                              placeholder="Mã bệnh nhân"
                              className="form-control form-control-sm"
                              aria-controls="chieldRow"
                              id="code"
                              autoComplete="off"
                              style={{ height: '37px' }}
                              value={patientCode}
                              onChange={changePatientCode}
                            />
                          </div>
                          <div className="col-md-2 pr-0">
                            <input
                              type="text"
                              placeholder="Tên bệnh nhân"
                              className="form-control form-control-sm"
                              aria-controls="chieldRow"
                              id="name"
                              autoComplete="off"
                              style={{ height: '37px' }}
                              value={patientName}
                              onChange={changePatientName}
                            />
                          </div>
                          <div className="col-md-3 pr-0">
                            <TekMultiSelect
                              items={cartTypesList}
                              getOptionLabel={getOptionLabel}
                              selectedValues={selectedTypes}
                              label=""
                              placeholder="Chọn thao tác"
                              selectAllLabel="Tất cả"
                              onToggleOption={handleToggleOption}
                              onClearOptions={handleClearOptions}
                              onSelectAll={handleSelectAll}
                            />
                          </div>
                          <div className="col-md-5">
                            <div className="btn-group mr-2">
                              <button
                                type="submit"
                                id="btnSearch"
                                className="btn btn-primary btn__search-mt"
                                disabled={
                                  _.isEmpty(patientCode) &&
                                  _.isEmpty(patientName)
                                }
                              >
                                Tìm kiếm <i className="fa fa-search"></i>
                              </button>
                            </div>
                            <div className="btn-group">
                              <button
                                className="btn btn-success btn__search-mt"
                                type="button"
                                onClick={() => {
                                  if (_.isEmpty(patientCode)) {
                                    notifyController
                                      .setNotifyMessage(
                                        'Vui lòng nhập mã bệnh nhân',
                                      )
                                      .setFailNotify();
                                    return;
                                  }
                                  dispatch(
                                    actions.exportTransactionCard({
                                      patient_code: patientCode,
                                    }),
                                  );
                                }}
                              >
                                Xuất file Excel<i className="fa fa-upload"></i>
                              </button>
                            </div>
                          </div>
                        </div>
                      </form>
                      <div className="table-scrollable">
                        <Paper className={classes.root}>
                          {loading ? <AppLoading loading={true} /> : null}
                          <TableContainer>
                            <Table stickyHeader className={classes.table}>
                              <STableHead>{renderColumns()}</STableHead>
                              <STableBody>{renderTable()}</STableBody>
                            </Table>
                          </TableContainer>
                          {renderPagination()}
                        </Paper>
                      </div>
                      {openPrintReturn && !_.isEmpty(cardInfo) ? (
                        <div className="d-none">
                          <PrintReturnHistory card={cardInfo} ref={printRef} />
                        </div>
                      ) : null}
                      <ModalConfirm
                        title={'Bạn có chắc chắn muốn hủy giao dịch này không?'}
                        other={{
                          bodyText: '',
                        }}
                        keyConfirm={''}
                        open={isOpenConfirm}
                        onClose={() => closeConfirm()}
                        confirmMethod={() => cancelPayment()}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
