/* eslint-disable @typescript-eslint/no-unused-expressions */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import * as _ from 'lodash';
import * as fromAuth from 'store/auth/shared/selectors';
import logo from 'img/logo-removebg-preview.png';
import moment from 'moment';
import { CurrencyService } from 'services/currency.service';
import { useSelector } from 'react-redux';

type Props = {
  info: any;
  startDate: any;
  endDate: any;
  adminView: any;
};

export const PdfOverView = React.forwardRef<HTMLDivElement, Props>(
  ({ info, startDate, endDate, adminView }, ref) => {
    if (_.isEmpty(info) && _.isEmpty(startDate) && _.isEmpty(endDate))
      return <></>;
    const user: any = useSelector<any>(fromAuth.selectUser);

    const renderHeaDer = () => {
      return (
        <header>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <div style={{ width: '120px', height: '120px' }}>
              <img src={logo} alt={logo} />
            </div>
            <div style={{ fontSize: '15px' }}>
              <p> TEKMEDI JSC </p>
              <p>
                {' '}
                The Sun Avenue SAV8 - 22.08, Số 28, Đường Mai Chí Thọ, Phường An
                Phú, Quận 2, Thành phố Hồ Chí Minh{' '}
              </p>
              <p>
                <span style={{ marginRight: '5px' }}> www.tekmedi.com </span>
                <span style={{ marginRight: '5px' }}>
                  service @tekmedi.com{' '}
                </span>{' '}
                <span style={{ marginRight: '5px' }}>(028) 3 930 1439 </span>
              </p>
            </div>
          </div>
          <div style={{ textAlign: 'center' }}>
            <p style={{ fontWeight: 'bold' }}>
              {' '}
              THỐNG KÊ GHI - NẠP - TRẢ - MẤT - THẺ
            </p>
          </div>
          <div style={{ textAlign: 'left' }}>
            <p style={{ fontWeight: 'bold' }}> Thônng tin giao dịch </p>
          </div>
          <div>
            <p>
              {' '}
              Từ ngày: {moment(startDate).format('DD-MM-YYYY')} đến ngày{' '}
              {moment(endDate).format('DD-MM-YYYY')}
            </p>
          </div>
        </header>
      );
    };

    const renderTable = () => {
      return info.data.map((row, idx) => {
        return (
          <tr key={idx}>
            <td colSpan={6}>{row.employee_name}</td>
            <td colSpan={2}>{row.total_register}</td>
            <td colSpan={2}>
              {CurrencyService.formatCurrency(row.total_price_register)}
            </td>
            <td colSpan={2}>{row.total_recharged}</td>
            <td colSpan={2}>
              {CurrencyService.formatCurrency(row.total_price_recharged)}
            </td>
            <td colSpan={2}>{row.total_return}</td>
            <td colSpan={2}>
              {CurrencyService.formatCurrency(row.total_price_return)}
            </td>
            <td colSpan={2}>{row.total_card_fee}</td>
            <td colSpan={2}>
              {CurrencyService.formatCurrency(row.total_price_card_fee)}
            </td>
          </tr>
        );
      });
    };

    const renderTotalAmount = () => {
      return (
        <div style={{ marginTop: '50px' }}>
          <p className="font-weight-bold">
            Tổng nộp tài chính :{' '}
            {CurrencyService.formatCurrency(info.total_amount_financial_dept)}
          </p>
          <p className="font-weight-bold">
            Tổng nộp Tekmedi :{' '}
            {CurrencyService.formatCurrency(info.total_amount_tekmedi)}
          </p>
        </div>
      );
    };

    const renderFooter = () => {
      return (
        <div
          style={{
            float: 'right',
            width: '450px',
            textAlign: 'center',
            marginTop: '50px',
          }}
        >
          <p>
            {' '}
            Bệnh viện Chợ Rẫy, {moment().format('HH')} giờ{' '}
            {moment().format('mm')} phút , Ngày {moment().format('DD')} tháng{' '}
            {moment().format('MM')} năm {moment().format('YYYY')}
          </p>
          <p style={{ marginTop: '50px' }}>
            {!_.isEmpty(user) ? user.full_name : ''}
          </p>
        </div>
      );
    };

    return (
      <div ref={ref} {...info} style={{ pageBreakAfter: 'always' }}>
        {renderHeaDer()}
        <table className="tablePdfOver">
          <thead>
            <tr className="tag-td-over">
              <td colSpan={6} rowSpan={2}>
                Nhân viên giao dịch
              </td>
              <td colSpan={4}>Thống kê ghi thẻ</td>
              <td colSpan={4}>Thống kê nạp tiền vào thẻ</td>
              <td colSpan={4}>Thống kê trả thẻ</td>
              <td colSpan={4}>Thống kê mất thẻ</td>
            </tr>
            <tr className="tag-td-over">
              <td colSpan={2}>Số lần ghi</td>
              <td colSpan={2}>Tổng số tiền</td>
              <td colSpan={2}>Số lần nạp tiền</td>
              <td colSpan={2}>Tổng số tiền</td>
              <td colSpan={2}>Số lần trả thẻ</td>
              <td colSpan={2}>Tổng số tiền</td>
              <td colSpan={2}>Số lần mất thẻ</td>
              <td colSpan={2}>Tổng số tiền</td>
            </tr>
          </thead>
          <tbody>{renderTable()}</tbody>
          {adminView ? (
            <tbody className="tableOverview">
              <tr>
                <td colSpan={6}>Tổng tiền</td>
                <td colSpan={4}></td>
                <td colSpan={4}>
                  {CurrencyService.formatCurrency(info.total_amount_recharged)}
                </td>
                <td colSpan={4}>
                  {CurrencyService.formatCurrency(info.total_amount_return)}
                </td>
                <td colSpan={4}>
                  {CurrencyService.formatCurrency(info.total_amount_card_fee)}
                </td>
              </tr>
            </tbody>
          ) : null}
        </table>
        {renderTotalAmount()}
        {renderFooter()}
      </div>
    );
  },
);
