/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useMemo, useRef } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as _ from 'lodash';
import * as fromAuth from 'store/auth/shared/selectors';
import { sliceKey, reducer, actions } from 'store/card/shared/slice';
import { CardFormSaga } from 'store/card/shared/saga';
import {
  selectDataCardStatistical,
  selectLoading,
  selectFilePdf,
  selectFilePdfOver,
  selectFilePdfCardFee,
  selectFilePdfRegister,
  selectFilePdfRecharged,
  selectFilePdfReturn,
} from 'store/card/shared/selectors';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { NotifyService } from 'services/notify.service';
import { CurrencyService } from 'services/currency.service';
import { useSnackbar } from 'notistack';
import { AppHelper, Order } from 'utils/app.helper';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { CardStatisticsModel } from 'store/card/shared/Model';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_DATE_TIME,
  DEFAULT_FORMAT_INPUT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
  StyledTableRow,
  StyledTableCell,
} from 'store/common/constants/common.constant';
import {
  CardTypes,
  tableStatiscalCard,
  report,
  TypeCard,
  defaultPagi,
  RESPONSE_MESSAGE,
} from 'store/card/constants/card.constant';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';
import viLocale from 'date-fns/locale/vi';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { PdfTypeCard } from '../../ReportPage/components/PdfTypeCard';
import { useReactToPrint } from 'react-to-print';
import { PdfOverView } from 'app/containers/ReportPage/components/PdfOverView';
import { PdfCardFee } from 'app/containers/ReportPage/components/PdfCardFee';
import { PdfRegister } from 'app/containers/ReportPage/components/PdfRegister';
import { PdfRecharged } from 'app/containers/ReportPage/components/PdfRecharged';
import { PdfReturn } from 'app/containers/ReportPage/components/PdfReturn';

export const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
  },
  table: {
    minWidth: '1600px',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  register: {
    color: 'blue',
    fontWeight: 'bold',
  },
  recharge: {
    color: 'green',
    fontWeight: 'bold',
  },
  return: {
    color: 'purple',
    fontWeight: 'bold',
  },
  cardFee: {
    color: '#b71616',
    fontWeight: 'bold',
  },
  cancel: {
    color: 'red',
    fontWeight: 'bold',
  },
});

export function StatisticalCard() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const dateUtil = new DateFnsUtils();
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: CardFormSaga });
  const user = useSelector(fromAuth.selectUser);
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const dataCards: any = useSelector<any>(selectDataCardStatistical);
  const filePdf: any = useSelector<any>(selectFilePdf);
  const filePdfOver: any = useSelector<any>(selectFilePdfOver);
  const filePdfCardFee: any = useSelector<any>(selectFilePdfCardFee);
  const filePdfRegister: any = useSelector<any>(selectFilePdfRegister);
  const filePdfRecharged: any = useSelector<any>(selectFilePdfRecharged);
  const filePdfReturn: any = useSelector<any>(selectFilePdfReturn);
  const loading: any = useSelector<any>(selectLoading);
  const [startDate, setStartDate] = useState<any>(new Date());
  const [endDate, setEndDate] = useState<any>(new Date());
  const [isOpenDisplay, setIsOpenDisplay] = useState(false);
  const [isCloseTable, setIsCloseTable] = useState(false);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [statusRp, setStatusRp] = useState<any>(report[0].code);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('name');

  let printRef = useRef<HTMLDivElement>(null);
  const reactToPrintContent = React.useCallback(() => {
    return printRef.current;
  }, [printRef.current]);
  const handlePrint = useReactToPrint!({
    content: reactToPrintContent,
    removeAfterPrint: true,
  });

  let printRefOv = useRef<HTMLDivElement>(null);
  const reactToPrintContentOv = React.useCallback(() => {
    return printRefOv.current;
  }, [printRefOv.current]);
  const handlePrintOv = useReactToPrint!({
    content: reactToPrintContentOv,
    removeAfterPrint: true,
  });

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case actions.getCardStatisticalSuccess.type:
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.GET_CARD_STATISTICAL_SUCCESS)
            .setSuccessNotify();
          break;
        case actions.exportHistoryCardSuccess.type:
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          notifyController
            .setNotifyMessage(RESPONSE_MESSAGE.EXPORT_HISTORY_CARD_SUCCESS)
            .setSuccessNotify();
          break;
        case actions.getCardStatisticalFailure.type:
          notifyController
            .setNotifyMessage(
              payload ? payload : RESPONSE_MESSAGE.GET_CARD_STATISTICAL_FAIL,
            )
            .setFailNotify();
          break;
        case actions.exportHistoryCardFail.type:
        case actions.exportHistoryCardPdfFail.type:
          notifyController
            .setNotifyMessage(
              payload ? payload : RESPONSE_MESSAGE.EXPORT_HISTORY_CARD_FAIL,
            )
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
      dispatch(actions.clearFilePdf());
      dispatch(actions.clearFilePdfOver());
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(user)) {
      dispatch(actions.getCardStatistical({ ...getFromValues() }));
    }
  }, [user]);

  useEffect(() => {
    if (!_.isEmpty(filePdf)) {
      handlePrint!();
    }
  }, [filePdf]);

  useEffect(() => {
    if (!_.isEmpty(filePdfOver)) {
      handlePrintOv!();
    }
  }, [filePdfOver]);

  const memoCard: any = useMemo((): any => {
    if (!_.isEmpty(dataCards)) {
      const { data = [], total_records } = dataCards;
      return { data, total_records };
    }
  }, [dataCards]);

  const getFromValues = () => {
    return {
      admin_View: false,
      type_Card: statusRp,
      start_Date: dateUtil.format(startDate, DEFAULT_FORMAT_INPUT_DATE),
      end_Date: dateUtil.format(endDate, DEFAULT_FORMAT_INPUT_DATE),
      employee_Id: user.id,
      employee_Name: user.full_name,
      page_Number: pagi.page_Number === 0 ? 1 : pagi.page_Number,
      page_Size: pagi.page_Size,
    };
  };

  const validateCardList = () =>
    dataCards.data && dataCards.data.length ? true : false;

  const submitSearch = e => {
    setPagi(defaultPagi);
    e.preventDefault();
    dispatch(
      actions.getCardStatistical({ ...getFromValues(), ...defaultPagi }),
    );
  };

  const handleIsOpenDisplay = () => {
    setIsOpenDisplay(!isOpenDisplay);
  };
  const handleCloseTable = () => {
    setIsCloseTable(!isCloseTable);
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleRefresh = () => {
    setStartDate(new Date());
    setEndDate(new Date());
    setStatusRp(report[0].code);
    setPagi(defaultPagi);
    dispatch(
      actions.getCardStatistical({
        ...getFromValues(),
        start_Date: new Date(),
        end_Date: new Date(),
        page_Number: 1,
        page_Size: 10,
        type_Card: report[0].code,
      }),
    );
  };

  const exportFileHistoryCards = () => {
    dispatch(actions.exportHistoryCard({ ...getFromValues() }));
  };

  const exportFileHistoryCardPdf = () => {
    if (statusRp !== '0') {
      dispatch(actions.exportHistoryCardPdf({ ...getFromValues() }));
    }
    if (statusRp === '0') {
      dispatch(actions.exportHistoryCardPdfOver({ ...getFromValues() }));
    }
  };

  const renderTableControl = () => {
    return (
      <form onSubmit={submitSearch} className="row">
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
          <div className="col-xl-2 col-lg-2 form-group">
            <label className="font-weight-bold">Thời gian bắt đầu</label>
            <DatePicker
              value={startDate}
              onChange={e => setStartDate(e)}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              maxDate={endDate}
            />
          </div>
          <span className="span__line">-</span>
          <div className="col-xl-2 col-lg-2 form-group">
            <label className="font-weight-bold">Thời gian kết thúc</label>
            <DatePicker
              value={endDate}
              onChange={e => setEndDate(e)}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              minDate={startDate}
              maxDate={new Date()}
            />
          </div>
        </MuiPickersUtilsProvider>
        <div className="col-xl-2 col-lg-2 form-group">
          <label className="font-weight-bold">Báo cáo</label>
          <FormControl fullWidth>
            <Select
              id="report"
              name="report"
              className="style_select"
              value={statusRp}
              variant="outlined"
              onChange={e => setStatusRp(e.target.value)}
            >
              {report.map(({ code, label }) => (
                <MenuItem value={code} key={code}>
                  {label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
        <div className="col-xl-5 col-lg-12 btn-group mg-t">
          <button className="btn btn-primary height39 mr-2" type="submit">
            Tìm kiếm <i className="fa fa-search"></i>
          </button>
          <button
            className="btn btn-success height39 mr-2"
            type="button"
            onClick={exportFileHistoryCards}
          >
            Xuất file Excel<i className="fa fa-upload"></i>
          </button>
          <button
            className="btn btn-danger height39"
            type="button"
            onClick={exportFileHistoryCardPdf}
          >
            Xuất File PDF<i className="fa fa-upload"></i>
          </button>
        </div>
      </form>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableStatiscalCard}
      />
    );
  };

  const renderTable = () => {
    if (validateCardList()) {
      const tableData: any[] = AppHelper.stableSort(
        memoCard.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, idx) => {
        const card = new CardStatisticsModel(row);
        return (
          <StyledTableRow key={idx} hover>
            {tableStatiscalCard.map(column => {
              const value = card[column.id];
              if (column.id === 'time') {
                return (
                  <StyledTableCell key={column.id}>
                    {value
                      ? dateUtil.format(
                          new Date(value),
                          DEFAULT_FORMAT_DATE_TIME,
                        )
                      : ''}
                  </StyledTableCell>
                );
              }
              if (column.id === 'price') {
                return (
                  <StyledTableCell key={column.id}>
                    {CurrencyService.formatCurrency(value)}
                  </StyledTableCell>
                );
              }
              if (column.id === 'type') {
                return (
                  <StyledTableCell key={column.id}>
                    <span
                      className={
                        value === CardTypes.REGISTER
                          ? classes.register
                          : value === CardTypes.RECHARGE
                          ? classes.recharge
                          : value === CardTypes.RETURN
                          ? classes.return
                          : value === CardTypes.CARDFEE
                          ? classes.cardFee
                          : value === CardTypes.CANCEL
                          ? classes.cancel
                          : '-'
                      }
                    >
                      {value === CardTypes.REGISTER
                        ? 'Ghi thẻ'
                        : value === CardTypes.RECHARGE
                        ? 'Nạp thẻ'
                        : value === CardTypes.RETURN
                        ? 'Trả thẻ'
                        : value === CardTypes.CARDFEE
                        ? 'Mất thẻ'
                        : value === CardTypes.CANCEL
                        ? 'Hủy Thẻ'
                        : '-'}
                    </span>
                  </StyledTableCell>
                );
              }
              if (column.id === 'payment_type_name') {
                return (
                  <StyledTableCell key={column.id}>
                    {row.type === 2 && _.isEmpty(value)
                      ? 'Tiền mặt'
                      : row.type === 2 && !_.isEmpty(value)
                      ? value
                      : '-'}
                  </StyledTableCell>
                );
              }
              return (
                <StyledTableCell key={column.id}>
                  {value ? value : '-'}
                </StyledTableCell>
              );
            })}
          </StyledTableRow>
        );
      });
    } else {
      return (
        <StyledTableRow>
          <StyledTableCell
            className="w-100"
            align="center"
            colSpan={tableStatiscalCard.length}
          >
            {EMPTY_MESSAGE}
          </StyledTableCell>
        </StyledTableRow>
      );
    }
  };

  const handleChangePage = (event: unknown, page_Number: number) => {
    let changePage = Object.assign({}, pagi, {
      page_Number: page_Number + 1,
      page_Size: pagi.page_Size,
    });
    setPagi(changePage);
    dispatch(
      actions.getCardStatistical({
        ...getFromValues(),
        ...changePage,
      }),
    );
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, pagi, {
      page_Size: +event.target.value,
      page_Number: 1,
    });
    setPagi(changeRowPerPage);
    dispatch(
      actions.getCardStatistical({
        ...getFromValues(),
        ...changeRowPerPage,
      }),
    );
  };
  const renderPagination = () => {
    if (validateCardList()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={memoCard.total_records}
          rowsPerPage={pagi.page_Size}
          page={pagi.page_Number - 1}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row" style={{ display: isCloseTable ? 'none' : 'block' }}>
      <div className="d-none">
        <PdfTypeCard
          ref={printRef}
          info={filePdf}
          startDate={startDate}
          endDate={endDate}
          adminView={false}
          typeCard={statusRp}
        />
        <div ref={printRefOv}>
          <PdfOverView
            info={filePdfOver}
            startDate={startDate}
            endDate={endDate}
            adminView={false}
          />
          <PdfRegister
            info={filePdfRegister}
            startDate={startDate}
            endDate={endDate}
            adminView={false}
            typeCard={TypeCard.WRITE_CARD}
          />
          <PdfRecharged
            info={filePdfRecharged}
            startDate={startDate}
            endDate={endDate}
            adminView={false}
            typeCard={TypeCard.UP_CARD}
          />
          <PdfReturn
            info={filePdfReturn}
            startDate={startDate}
            endDate={endDate}
            adminView={false}
            typeCard={TypeCard.RETURN_CARD}
          />
          <PdfCardFee
            info={filePdfCardFee}
            startDate={startDate}
            endDate={endDate}
            adminView={false}
            typeCard={TypeCard.LOST_CARD}
          />
        </div>
      </div>
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head">
            <div className="tools">
              <a
                className="fa fa-repeat btn-color box-refresh"
                onClick={handleRefresh}
              ></a>
              <a
                className={
                  isOpenDisplay
                    ? 't-collapse btn-color fa fa-chevron-down'
                    : 't-collapse btn-color fa fa-chevron-up'
                }
                onClick={handleIsOpenDisplay}
              ></a>
              <a
                className="t-close btn-color fa fa-times"
                onClick={handleCloseTable}
              ></a>
            </div>
          </div>
          <div
            className="card-body"
            style={{ display: isOpenDisplay ? 'none' : 'block' }}
          >
            {renderTableControl()}
            <Paper className={classes.root}>
              {loading && <AppLoading loading={true} />}
              <TableContainer className={classes.container}>
                <Table stickyHeader>
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
          </div>
        </div>
      </div>
    </div>
  );
}
