/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
import { createSlice } from '../../../store/core/@reduxjs/toolkit';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_STATISTICAL_CONSTANT,
  RESPONSE_STATISTICAL_MESSAGE,
} from '../constants/statistical.constant';

export interface StatisticalState {
  loading?: boolean;
  success?: any;
  error?: any;
  listStatistical: any;
  filesExcell: any;
}

export const initialState: StatisticalState = {
  loading: false,
  success: null,
  error: null,
  listStatistical: {},
  filesExcell: {},
};

const StatisticalSlice = createSlice({
  name: 'statisticalPatient',
  initialState,
  reducers: {
    statisticalPatient(state, action) {
      state.loading = true;
    },

    statisticalPatientSuccess(state, action) {
      state.loading = false;
      state.listStatistical = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_STATISTICAL_CONSTANT.STATISTICAL_PATIENT_SUCCESS,
        message: RESPONSE_STATISTICAL_MESSAGE.STATISTICAL_PATIENT_SUCCESS,
      };
    },

    statisticalPatientFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_STATISTICAL_CONSTANT.STATISTICAL_PATIENT_FAIL,
        message: action.payload,
      };
    },

    exportStatistical(state, action) {
      state.loading = true;
    },

    exportStatisticalSuccess(state, action) {
      state.loading = false;
      state.filesExcell = action.payload;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_STATISTICAL_CONSTANT.EXPORT_STATISTICAL_SUCCESS,
        message: RESPONSE_STATISTICAL_MESSAGE.EXPORT_STATISTICAL_SUCCESS,
      };
    },

    exportStatisticalFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_STATISTICAL_CONSTANT.EXPORT_STATISTICAL_FAIL,
        message: action.payload,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearError(state) {
      state.error = {};
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearListStatistical(state) {
      state.listStatistical = {};
    },

    resetAll(state) {
      state.loading = false;
      state.success = null;
      state.error = null;
    },
  },
});

export const { actions, reducer, name: sliceKey } = StatisticalSlice;
