/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useMemo, useReducer } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import * as _ from 'lodash';
import * as StatisticalSelector from 'store/statisticalPatient/shared/selectors';
import * as StatisticalSlice from 'store/statisticalPatient/shared/slice';
import * as StatisticalConts from 'store/statisticalPatient/constants/statistical.constant';
import * as fromAuth from 'store/auth/shared/selectors';
import { StatisticalSaga } from 'store/statisticalPatient/shared/saga';
import { makeStyles } from '@material-ui/core/styles';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { TekDatePicker } from 'app/components/InputDatePicker';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { AppHelper, Order } from 'utils/app.helper';
import { useForm, Controller } from 'react-hook-form';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_INPUT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import TableHeaderSort from 'app/components/TableHeaderSort';
import viLocale from 'date-fns/locale/vi';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  table: {
    width: '100%',
    minWidth: '1600px',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

export const tableStatistical = [
  { id: 'patient_code', label: 'Mã bệnh nhân', minWidth: 100 },
  { id: 'full_name', label: 'Tên bệnh nhân', minWidth: 120 },
  { id: 'initial', label: 'Đầu kỳ', minWidth: 120 },
  { id: 'increase', label: 'Tăng kỳ', minWidth: 150 },
  { id: 'payment', label: 'Giảm kỳ (Thanh toán)', minWidth: 150 },
  { id: 'return_card_amount', label: 'Giảm kỳ (Trả thẻ)', minWidth: 150 },
  { id: 'last_amount', label: 'Cuối kỳ', minWidth: 150 },
];

interface ModalState {
  openModal: boolean;
}

enum ModalType {
  OPEN = 'OPEN',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  openModal: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN:
      return {
        ...state,
        openModal: payload,
      };
    default:
      return state;
  }
};

const defaultPagi = {
  page: 0,
  rowsPerPage: 10,
};

export function StatisticalPatient() {
  const classes = useStyles();
  const dateUtil = new DateFnsUtils();
  const dispatch = useDispatch();
  useInjectReducer({
    key: StatisticalSlice.sliceKey,
    reducer: StatisticalSlice.reducer,
  });
  useInjectSaga({ key: StatisticalSlice.sliceKey, saga: StatisticalSaga });
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const user = useSelector(fromAuth.selectUser);
  const { handleSubmit, register, control, setValue, getValues } = useForm();
  const loading = useSelector(StatisticalSelector.selectLoading);
  const listStatistical = useSelector(
    StatisticalSelector.selectListStatistical,
  );
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [order, setOrder] = React.useState<Order>('asc');
  const [orderBy, setOrderBy] = React.useState<any>('tekmedi_card_number');
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [errDate, setErrDate] = useState<any>(false);

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case StatisticalSlice.actions.statisticalPatientSuccess.type:
          notifyController
            .setNotifyMessage(
              StatisticalConts.RESPONSE_STATISTICAL_MESSAGE
                .STATISTICAL_PATIENT_SUCCESS,
            )
            .setSuccessNotify();
          break;
        case StatisticalSlice.actions.exportStatisticalSuccess.type:
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case StatisticalSlice.actions.statisticalPatientFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : StatisticalConts.RESPONSE_STATISTICAL_MESSAGE
                    .STATISTICAL_PATIENT_FAIL,
            )
            .setFailNotify();
          break;
        case StatisticalSlice.actions.exportStatisticalFailure.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : StatisticalConts.RESPONSE_STATISTICAL_MESSAGE
                    .EXPORT_STATISTICAL_FAIL,
            )
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  const getFormValues = (): any => {
    return {
      employee_Id: _.get(user, 'id'),
      employee_Name: _.get(user, 'full_name') || '',
      start_Date: dateUtil.format(startDate, DEFAULT_FORMAT_INPUT_DATE),
      end_Date: dateUtil.format(endDate, DEFAULT_FORMAT_INPUT_DATE),
      is_Export: false,
      draw: pagi.page,
      start: 1,
      length: pagi.rowsPerPage,
      search: {
        value: '',
        regex: true,
      },
    };
  };

  const handleReset = () => {
    setValue('search', null);
    setStartDate(new Date());
    setEndDate(new Date());
    setPagi(defaultPagi);
    setErrDate(false);
    dispatch(StatisticalSlice.actions.clearListStatistical());
  };

  const submitSearch = value => {
    dispatch(
      StatisticalSlice.actions.statisticalPatient({
        ...getFormValues(),
        search: { value: value.search },
      }),
    );
  };

  const handleExport = () => {
    dispatch(
      StatisticalSlice.actions.exportStatistical({
        ...getFormValues(),
        employee_Id: _.get(user, 'id'),
        employee_Name: _.get(user, 'full_name') || '',
        is_Export: true,
        search: {
          value: '',
          regex: true,
        },
      }),
    );
  };

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangePage = (event, page) => {
    let changePage = Object.assign({}, pagi, { page });
    setPagi(changePage);
    dispatch(
      StatisticalSlice.actions.statisticalPatient({
        ...getFormValues(),
        draw: changePage.page,
        start: changePage.page + 1,
      }),
    );
  };

  const handleChangeRowsPerPage = e => {
    let changeRowPage = Object.assign({}, pagi, {
      rowsPerPage: +e.target.value,
      page: 0,
    });
    setPagi(changeRowPage);
    dispatch(
      StatisticalSlice.actions.statisticalPatient({
        ...getFormValues(),
        draw: changeRowPage.page,
        start: 1,
        length: changeRowPage.rowsPerPage,
      }),
    );
  };

  const renderData: any = useMemo(() => {
    if (!_.isEmpty(listStatistical)) {
      const { data = [], recordsTotal } = listStatistical;
      return {
        recordsTotal,
        data,
      };
    }
  }, [listStatistical]);

  const handleStartTime = (date: any) => {
    if (date > endDate) {
      setErrDate(true);
      setStartDate(new Date(date));
    } else {
      setErrDate(false);
      setStartDate(new Date(date));
    }
  };

  const handleEndTime = (date: any) => {
    if (date < startDate) {
      setErrDate(true);
      setEndDate(new Date(date));
    } else {
      setErrDate(false);
      setEndDate(new Date(date));
    }
  };

  const renderTableControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <form onSubmit={handleSubmit(submitSearch)}>
          <div className="row">
            <div className="col-md-3">
              <label className="font-weight-bold" htmlFor="search">
                Nhập Tên/Mã
              </label>
              <input
                type="text"
                className="form-control"
                name="search"
                placeholder="Tìm theo Tên/Mã"
                ref={register}
              />
            </div>
            <div className="col-md-2">
              <label className="font-weight-bold">Ngày bắt đầu</label>
              <Controller
                name="startDate"
                control={control}
                disableFuture
                render={({ field, ...input }: any) => {
                  return (
                    <DatePicker
                      value={startDate}
                      format={DEFAULT_FORMAT_DATE}
                      TextFieldComponent={TekDatePicker}
                      placeholder="Tháng bắt đầu"
                      maxDate={new Date()}
                      onChange={date => {
                        handleStartTime(date);
                      }}
                    />
                  );
                }}
              />
              {errDate && (
                <span style={{ color: ' #fb0606' }}>
                  Ngày bắt đầu phải nhỏ hơn ngày kết thúc
                </span>
              )}
            </div>
            <span className="span__line">-</span>
            <div className="col-md-2">
              <label className="font-weight-bold">Ngày kết thúc</label>
              <Controller
                name="endDate"
                control={control}
                disableFuture
                render={({ field, ...input }: any) => {
                  return (
                    <DatePicker
                      value={endDate}
                      format={DEFAULT_FORMAT_DATE}
                      TextFieldComponent={TekDatePicker}
                      placeholder="Tháng bắt đầu"
                      maxDate={new Date()}
                      onChange={date => {
                        handleEndTime(date);
                      }}
                    />
                  );
                }}
              />
            </div>
            <div className="col-md-4">
              <div className="btn-group mg-t mr-1">
                <button
                  className="btn btn-success height37"
                  type="submit"
                  disabled={errDate}
                >
                  Tìm kiếm <i className="fa fa-search"></i>
                </button>
              </div>
              <div className="btn-group mg-t mr-1">
                <button
                  className="btn btn-warning height37"
                  type="button"
                  onClick={handleReset}
                >
                  Reset <i className="fa fa-refresh"></i>
                </button>
              </div>
              <div className="btn-group mg-t">
                <button
                  type="button"
                  className="btn btn-danger height37"
                  disabled={errDate}
                  onClick={handleExport}
                >
                  Export <i className="fa fa-upload"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={tableStatistical}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(renderData) || !renderData.data.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={tableStatistical.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      renderData.data,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((row, idx) => {
      const payment = _.cloneDeep(row);
      return (
        <TableRow key={idx}>
          {tableStatistical.map(column => {
            const value = payment[column.id];
            if (
              column.id === 'increase' ||
              column.id === 'initial' ||
              column.id === 'last_amount' ||
              column.id === 'payment' ||
              column.id === 'return_card_amount'
            ) {
              return (
                <TableCell key={column.id}>
                  {AppHelper.formatCurrency(value)}
                </TableCell>
              );
            }
            return <TableCell key={column.id}>{value ? value : '-'}</TableCell>;
          })}
        </TableRow>
      );
    });
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.recordsTotal}
          rowsPerPage={pagi ? pagi.rowsPerPage : 10}
          page={pagi ? pagi.page : 0}
          labelRowsPerPage={'Hiển Thị'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      {loading && <AppLoading loading={true} />}
      <div className="col-12">
        <div className="card card-topline-red">
          <div className="card-body">
            {renderTableControl()}
            <Paper className={classes.root}>
              <TableContainer className={classes.container}>
                <Table className={classes.table}>
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
          </div>
        </div>
      </div>
    </div>
  );
}
