/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
// eslint-disable react-hooks/exhaustive-deps
import React, { useState, useEffect } from 'react';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import * as _ from 'lodash';
import * as TransactionSlice from 'store/transaction/shared/slice';
import * as fromTransaction from 'store/transaction/shared/selectors';
import * as fromAuth from 'store/auth/shared/selectors';
import { useSelector, useDispatch } from 'react-redux';
import { AppHelper, Order } from 'utils/app.helper';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import {
  useStyles,
  DEFAULT_FORMAT_DATE_TIME,
} from 'store/common/constants/common.constant';
import { CurrencyService } from 'services/currency.service';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import { TekDatePicker } from 'app/components/InputDatePicker';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useForm, Controller } from 'react-hook-form';
import { PaidWaitingFormSaga as TransactionSaga } from 'store/transaction/shared/saga';
import AppLoading from 'app/components/Loading';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableHeaderSort from 'app/components/TableHeaderSort';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';

const EXPORT_SUCCESS = 'Xuất file thành công';
const EXPORT_FAIL = 'Xuất file thất bại';

export const patientCols = [
  { id: 'no', label: '#', minWidth: 50 },
  { id: 'patient_code', label: 'Mã bệnh nhân', minWidth: 200 },
  { id: 'patient_name', label: 'Họ và tên', minWidth: 250 },
  {
    id: 'gender',
    label: 'Giới tính',
    minWidth: 130,
  },
  { id: 'registered_code', label: 'Số tiếp nhận', minWidth: 150 },
  { id: 'registered_date', label: 'Ngày đăng ký', minWidth: 200 },
  { id: 'created_date', label: 'Ngày tiếp nhận', minWidth: 200 },
  { id: 'employee_name', label: 'Tên nhân viên', minWidth: 250 },
  { id: 'price', label: 'Tiền thuốc', minWidth: 200 },
];

export const defaultPagi = {
  pageSize: 10,
  pageNumber: 1,
};

export const defaultFilter = {
  startDate: new DateFnsUtils().addDays(new Date(), -30),
  endDate: new Date(),
  employeeId: '',
  employeeName: '',
};

export function MedicineReport() {
  useInjectReducer({
    key: TransactionSlice.sliceKey,
    reducer: TransactionSlice.reducer,
  });
  useInjectSaga({ key: TransactionSlice.sliceKey, saga: TransactionSaga });
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const userStore = useSelector(fromAuth.selectUser);
  const medicines: any = useSelector(fromTransaction.selectMedicines);
  const loading: boolean = useSelector(fromTransaction.selectLoading);
  const classes = useStyles();
  const dateUtil = new DateFnsUtils();
  const dispatch = useDispatch();
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('code');
  const { handleSubmit, control, setValue, getValues } = useForm({
    defaultValues: {
      ...defaultFilter,
    },
  });
  const [pagi, setPagi] = useState<any>({});
  const [start, setStart] = useState<any>(defaultFilter.startDate);
  const [end, setEnd] = useState<any>(defaultFilter.endDate);

  useEffect(() => {
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case TransactionSlice.actions.exportMedicineSuccess.type:
          notifyController.setNotifyMessage(EXPORT_SUCCESS).setSuccessNotify();
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case TransactionSlice.actions.exportMedicineFail.type:
          notifyController.setNotifyMessage(EXPORT_FAIL).setFailNotify();
          break;
        case TransactionSlice.actions.getMedicineReportFail.type:
          notifyController
            .setNotifyMessage(payload ? payload : 'ERROR')
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    return () => {
      storeSub$();
    };
  }, []);

  useEffect(() => {
    function setUser(user) {
      if (_.isEmpty(user)) return;
      getMedicine(defaultPagi, {
        ...defaultFilter,
        ...setUserFilter(user),
      });
    }
    setUser(userStore);
  }, [userStore]);

  const getFormValues = () => {
    return {
      startDate: getValues('startDate'),
      endDate: getValues('endDate'),
    };
  };

  const setUserFilter = (user: any) => {
    const { id, full_name } = user;
    return {
      employeeId:
        process.env.NODE_ENV === 'development'
          ? ' 65e929ab-856f-11ea-916f-005056b65f94'
          : id,
      employeeName: full_name,
    };
  };

  const getMedicine = (newPagi: any, formSearch) => {
    setPagi(newPagi);
    dispatch(
      TransactionSlice.actions.getMedicineReport({
        ...newPagi,
        ...formSearch,
        ...setUserFilter(userStore),
      }),
    );
  };

  const handleChangePage = (event: unknown, pageNumber: number) => {
    let newPagi = Object.assign({}, pagi, {
      pageNumber: pageNumber + 1,
    });
    getMedicine(newPagi, getFormValues());
  };

  const handleChangeRowsPerPage = event => {
    let newPagi = Object.assign({}, pagi, {
      pageSize: +event.target.value,
      pageNumber: 1,
    });
    getMedicine(newPagi, getFormValues());
  };

  const downloadExport = () => {
    dispatch(
      TransactionSlice.actions.exportMedicine({
        ...pagi,
        ...getFormValues(),
        ...setUserFilter(userStore),
      }),
    );
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleReset = () => {
    setValue('startDate', new DateFnsUtils().addDays(new Date(), -30));
    setValue('endDate', new Date());
  };

  const renderTableControl = () => {
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
        <form
          onSubmit={handleSubmit(values =>
            getMedicine(defaultPagi, {
              ...values,
              ...setUserFilter(userStore),
            }),
          )}
        >
          <div className="row" style={{ justifyContent: 'space-between' }}>
            <div className="col-md-10 row">
              <div className="col-md-3">
                <label className="font-weight-bold">Thời gian</label>
                <Controller
                  name="startDate"
                  control={control}
                  disableFuture
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày bắt đầu"
                        maxDate={end}
                        onChange={val => {
                          setValue('startDate', val);
                          setStart(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
              <span className="span__line">-</span>
              <div className="col-md-3 mg-t">
                <Controller
                  name="endDate"
                  control={control}
                  defaultValue={null}
                  render={({ field, ...input }: any) => {
                    return (
                      <DatePicker
                        value={input.value}
                        format={DEFAULT_FORMAT_DATE}
                        TextFieldComponent={TekDatePicker}
                        placeholder="Ngày kết thúc"
                        minDate={start}
                        onChange={val => {
                          setValue('endDate', val);
                          setEnd(val);
                        }}
                      />
                    );
                  }}
                />
              </div>
              <div className="col-md-5">
                <div className="btn-group mr-2">
                  <button
                    className="btn btn-primary mr-3 btn__search"
                    type="submit"
                  >
                    Tìm kiếm<i className="fa fa-search"></i>
                  </button>
                </div>
                <div className="btn-group">
                  <button
                    className="btn btn-warning mr-3 btn__search"
                    type="button"
                    onClick={handleReset}
                  >
                    Reset
                  </button>
                </div>
              </div>
            </div>
            <div className="col-md-2 export__examination text-right">
              <div className="btn-group">
                <button
                  aria-controls="simple-menu"
                  aria-haspopup="true"
                  type="button"
                  className="btn btn-success btn__export"
                  onClick={downloadExport}
                >
                  Xuất file <i className="fa fa-download"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </MuiPickersUtilsProvider>
    );
  };

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={patientCols}
      />
    );
  };

  const renderTable = () => {
    if (_.isEmpty(medicines) || !medicines.data.length) {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={patientCols.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
    const tableData: any[] = AppHelper.stableSort(
      medicines.data,
      AppHelper.getComparator(order, orderBy),
    );
    return tableData.map((patient, _id) => {
      return (
        <TableRow key={patient.id} hover>
          {patientCols.map(column => {
            const { page_number, page_size } = medicines;
            const value = patient[column.id];
            if (column.id === 'no') {
              return (
                <TableCell key={column.id} className={classes.td}>
                  {page_number === 1
                    ? page_number + _id
                    : _id + 1 + (page_number - 1) * page_size}
                </TableCell>
              );
            }
            if (column.id === 'created_date') {
              return (
                <TableCell key={column.id} className={classes.td}>
                  {value
                    ? dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE_TIME)
                    : '-'}
                </TableCell>
              );
            }
            if (column.id === 'registered_date') {
              return (
                <TableCell key={column.id} className={classes.td}>
                  {value
                    ? dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE)
                    : '-'}
                </TableCell>
              );
            }
            if (column.id === 'price') {
              return (
                <TableCell key={column.id} className={classes.td}>
                  {CurrencyService.formatCurrency(value)}
                </TableCell>
              );
            }
            return (
              <TableCell key={column.id} className={classes.td}>
                {value ? value : '-'}
              </TableCell>
            );
          })}
        </TableRow>
      );
    });
  };

  const renderPagination = () => {
    if (!_.isEmpty(medicines) && medicines.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          labelRowsPerPage="Hiển Thị"
          count={medicines.total_records}
          rowsPerPage={medicines?.page_size}
          page={medicines?.page_number - 1}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  return (
    <div className="row">
      <div className="col-md-12">
        <div className="row">
          <div className="col-md-12">
            <div className="card card-topline-red">
              <div className="card-head">
                <div className="tools"></div>
              </div>
              <div className="card-body">
                {renderTableControl()}
                <Paper className={classes.root}>
                  {loading ? <AppLoading loading={true} /> : null}
                  <TableContainer className={classes.container}>
                    <Table stickyHeader aria-label="sticky table">
                      <TableHead>{renderColumns()}</TableHead>
                      <TableBody>{renderTable()}</TableBody>
                    </Table>
                  </TableContainer>
                  {renderPagination()}
                </Paper>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
