import {
  PatientApiRoutes,
  defaultGetListPatient,
  defaultCreatePatientId,
} from '../constants/patient.constant';
import { HttpRequest, configRequest } from 'utils/request';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import { AppHelper } from 'utils/app.helper';
import { configResponse } from 'utils/request';

export class PatientHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
  }

  updateBalance = async (data: any): Promise<any> => {
    return await this.request.post(
      PatientApiRoutes.PATIENT.UPDATE_BALANCE,
      data,
    );
  };

  getPatientWithCode = async (
    code: string,
    recalcPrice?: boolean,
  ): Promise<any> => {
    const res = await this.request.get(
      PatientApiRoutes.PATIENT.GET_PATIENT_WITH_CODE + '/' + code,
    );
    if (!recalcPrice) return res;
    if (res?.ok) {
      // https://jira.tekmedi.com/browse/CRBTC-977
      if (res?.data?.result?.code) {
        res.data.result.recalcPrice = 123;
        res.data.result.diffAmount = 0;
        return res;
        // Remove get balance
        // const price = await this.getBalanceByPatient(res.data.result.code);
        // res.data.result.recalcPrice = price?.balance || 0;
        // res.data.result.diffAmount = price?.difference_amount || 0;
        // return res;
      } else {
        throw new Error('Not found patient code');
      }
    } else {
      throw res.data;
    }
  };

  getPatientWithType = ({ patientCode, requestDate }): Promise<any> => {
    return this.request.post(PatientApiRoutes.PATIENT.GET_BY_TYPE, {
      patient_code: patientCode.trim(' '),
      request_date: requestDate,
    });
  };

  getPatientWithCardCode = async (
    cardCode: string,
    recalcPrice?: boolean,
  ): Promise<any> => {
    const res = await this.request.get(
      PatientApiRoutes.PATIENT.GET_BY_CARD_NUMBER + '/' + cardCode,
    );
    if (!recalcPrice) return res;
    // https://jira.tekmedi.com/browse/CRBTC-977
    if (res?.ok) {
      if (res?.data?.result?.code) {
        res.data.result.recalcPrice = res.data.result.price;
        res.data.result.diffAmount = 0;
        return res;
        // Remove get balance
        // const price = await this.getBalanceByPatient(res.data.result.code);
        // res.data.result.recalcPrice = price?.balance || 0;
        // res.data.result.diffAmount = price?.difference_amount || 0;
        // return res;
      } else {
        throw new Error('Not found patient code');
      }
    } else {
      throw res.data;
    }
  };

  getPatientWithHealthId = (healthId: string): Promise<any> => {
    return this.request.get(
      PatientApiRoutes.PATIENT.GET_BY_HEALTH_ID + '/' + healthId,
    );
  };

  getListPatient = (filter: any): Promise<any> => {
    return this.request.get(
      PatientApiRoutes.PATIENT.GET_LIST_PATIENT,
      configRequest(filter),
    );
  };

  exportListPatient = (filter: any): Promise<any> => {
    return this.request.get(
      PatientApiRoutes.PATIENT.EXPORT_LIST_PATIENT,
      configRequest(filter),
    );
  };

  syncPatient = (patient_id: string): Promise<any> => {
    return this.request.put(
      PatientApiRoutes.PATIENT.SYNC_PATIENT + '?id=' + patient_id,
    );
  };

  deletePatient = (id: string): Promise<any> => {
    return this.request.post(
      `${PatientApiRoutes.PATIENT.DELETE_PATIENT}?id=${id}`,
    );
  };

  changePatientStatus = ({ id, is_active }): Promise<any> => {
    return this.request.post(PatientApiRoutes.PATIENT.CHANGE_STATUS, {
      id,
      is_active,
    });
  };

  findPatientWithId = (id: string): Promise<any> => {
    return this.request.get(
      `${PatientApiRoutes.PATIENT.FIND_PATIENT_BY_ID}?id=${id}`,
    );
  };

  checkUniquePatientCode = ({ code }): Promise<any> => {
    return this.request.post(PatientApiRoutes.PATIENT.CHECK_UNIQUE_CODE, {
      code,
      ...defaultCreatePatientId,
    });
  };

  createPatient = (patientInfo: any): Promise<any> => {
    return this.request.post(
      PatientApiRoutes.PATIENT.CREATE_PATIENT,
      patientInfo,
    );
  };

  updatePatient = (patientInfo: any): Promise<any> => {
    return this.request.post(
      PatientApiRoutes.PATIENT.UPDATE_PATIENT + '/' + patientInfo.id,
      patientInfo,
    );
  };
  changePatientVip = ({ id, patient_type, employee_id }: any): Promise<any> => {
    return this.request.post(
      PatientApiRoutes.PATIENT.CHANGE_PATIENT_TYPE + id,
      { patient_type, employee_id },
    );
  };

  getPatientBalance = ({
    startDate,
    endDate,
    userId,
    userName,
    page,
    rowsPerPage,
  }): Promise<any> => {
    return this.request.post(PatientApiRoutes.PATIENT.GET_BALANCE_PATIENT, {
      draw: 5,
      start: page * rowsPerPage,
      length: rowsPerPage,
      search: {
        regex: false,
        value: '',
      },
      employee_id: userId,
      employee_name: userName,
      end_date: AppHelper.formDateTimePost(endDate),
      order: [],
      start_date: AppHelper.formDateTimePost(startDate),
      columns: defaultGetListPatient,
    });
  };

  exportPatientBalance = ({
    userId,
    userName,
    startDate,
    endDate,
  }): Promise<any> => {
    return this.request.post(
      PatientApiRoutes.PATIENT.EXPORT_LIST_BALANCE_PATIENT,
      {
        employee_id: userId,
        employee_name: userName,
        end_date: AppHelper.formDateTimePost(endDate),
        start_date: AppHelper.formDateTimePost(startDate),
      },
    );
  };

  getPatientWithQRCode = (code: string): Promise<any> => {
    return this.request.post(
      PatientApiRoutes.PATIENT.GET_PATIENT_WITH_QR_CODE,
      { code },
    );
  };

  addTempPatient = ({
    firstName,
    lastName,
    gender,
    birthDay,
    wardId,
    provinceId,
    distId,
    patientType,
  }): Promise<any> => {
    return this.request.post(PatientApiRoutes.PATIENT.ADD_TEMP_PATIENT, {
      type: patientType,
      first_name: firstName,
      last_name: lastName,
      ward_id: wardId,
      province_id: provinceId,
      district_id: distId,
      gender,
      birthday: birthDay,
      phone: '',
      tekmediCardNumber: '',
      healthInsuranceNumber: '',
      healthInsuranceExpiredDate: '',
      healthInsuranceFirstPlaceCode: '',
      healthInsuranceFirstPlace: '',
      healthInsuranceIssuedDate: '',
    });
  };

  getBalanceByPatient = async (patientCode: string): Promise<any> => {
    return configResponse(
      await this.request.get(
        `${PatientApiRoutes.PATIENT.GET_BALANCE_BY_PATIENT}/${patientCode}`,
      ),
    );
  };
}
