/* eslint-disable @typescript-eslint/no-unused-expressions */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React from 'react';
import * as _ from 'lodash';
import * as fromAuth from 'store/auth/shared/selectors';
import logo from 'img/logo-removebg-preview.png';
import moment from 'moment';
import { CurrencyService } from 'services/currency.service';
import { useSelector } from 'react-redux';

type Props = {
  info: any;
  startDate: any;
  endDate: any;
  adminView: boolean;
  typeCard: any;
};

enum TypeCard {
  WRITE_CARD = 'GHI THẺ',
  UP_CARD = 'NẠP THẺ',
  RETURN_CARD = 'TRẢ THẺ',
  LOST_CARD = 'MẤT THẺ',
}

export const PdfTypeCard = React.forwardRef<HTMLDivElement, Props>(
  ({ info, startDate, endDate, adminView, typeCard }, ref) => {
    if (_.isEmpty(info) && _.isEmpty(startDate) && _.isEmpty(endDate))
      return <></>;
    const user: any = useSelector<any>(fromAuth.selectUser);

    const renderTypeCard = type => {
      switch (type) {
        case '1':
          return <span>{TypeCard.WRITE_CARD}</span>;
        case '2':
          return <span>{TypeCard.UP_CARD}</span>;
        case '3':
          return <span>{TypeCard.RETURN_CARD}</span>;
        case '11':
          return <span>{TypeCard.LOST_CARD}</span>;
        default:
          break;
      }
    };

    const renderHeaDer = () => {
      return (
        <header>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <div style={{ width: '120px', height: '120px' }}>
              <img src={logo} alt={logo} />
            </div>
            <div style={{ fontSize: '15px' }}>
              <p> TEKMEDI JSC </p>
              <p>
                {' '}
                The Sun Avenue SAV8 - 22.08, Số 28, Đường Mai Chí Thọ, Phường An
                Phú, Quận 2, Thành phố Hồ Chí Minh{' '}
              </p>
              <p>
                <span style={{ marginRight: '5px' }}> www.tekmedi.com </span>
                <span style={{ marginRight: '5px' }}>
                  service @tekmedi.com{' '}
                </span>{' '}
                <span style={{ marginRight: '5px' }}>(028) 3 930 1439 </span>
              </p>
            </div>
          </div>
          <div style={{ textAlign: 'center' }}>
            <p style={{ fontWeight: 'bold' }}>
              {' '}
              THỐNG KÊ CHI TIẾT {renderTypeCard(typeCard)}{' '}
            </p>
          </div>
          <div style={{ textAlign: 'left' }}>
            <p style={{ fontWeight: 'bold' }}> Thônng tin giao dịch </p>
          </div>
          <div>
            <p>
              {' '}
              Từ ngày: {moment(startDate).format('DD-MM-YYYY')} đến ngày{' '}
              {moment(endDate).format('DD-MM-YYYY')}
            </p>
          </div>
        </header>
      );
    };

    const renderAdminView = () => {
      if (adminView && typeCard !== '2') {
        return (
          <table className="tablePdf-admin">
            <thead>
              <tr className="tag-td-admin">
                <th colSpan={6}>Tổng số nhân viên</th>
                <th colSpan={5}>Tổng số lần thực hiện</th>
                <th colSpan={5}>Tổng số Bệnh nhân</th>
                <th colSpan={6}>Tổng số tiền trả thẻ</th>
              </tr>
            </thead>
            <tbody>
              <tr className="tag-td-data">
                <td colSpan={6}>
                  {CurrencyService.formatNumber(info.total_employee)}
                </td>
                <td colSpan={5}>
                  {CurrencyService.formatNumber(info.total_perform)}
                </td>
                <td colSpan={5}>
                  {CurrencyService.formatNumber(info.total_patient)}
                </td>
                <td colSpan={6} className="font-weight-bold">
                  {CurrencyService.formatCurrency(info.total_price)}
                </td>
              </tr>
            </tbody>
          </table>
        );
      }
      if (adminView && typeCard === '2') {
        return (
          <table className="tablePdf-admin">
            <thead>
              <tr className="tag-td-admin">
                <th colSpan={3}>Tổng số nhân viên</th>
                <th colSpan={3}>Tổng số lần thực hiện</th>
                <th colSpan={3}>Tổng số Bệnh nhân</th>
                <th colSpan={8}>Tổng số chi tiết loại hình</th>
                <th colSpan={6}>Tổng số tiền trả thẻ</th>
              </tr>
            </thead>
            <tbody>
              <tr className="tag-td-data">
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_employee)}
                </td>
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_perform)}
                </td>
                <td rowSpan={2} colSpan={3}>
                  {CurrencyService.formatNumber(info.total_patient)}
                </td>
                <td colSpan={2}>Tiền mặt</td>
                <td colSpan={2}>
                  {CurrencyService.formatNumber(info.total_card_scan)}
                </td>
                <td rowSpan={2} colSpan={6} className="font-weight-bold">
                  {CurrencyService.formatCurrency(info.total_price)}
                </td>
              </tr>
              <tr>
                <td colSpan={2}>Chuyển khoản</td>
                <td colSpan={2}>
                  {CurrencyService.formatNumber(info.total_transfer)}
                </td>
              </tr>
            </tbody>
          </table>
        );
      }
    };

    const renderTable = () => {
      return info.data_groups.map((row, idx) => {
        return (
          <tbody key={idx}>
            {adminView ? (
              <tr>
                <td colSpan={19} className="text-left font-weight-bold">
                  {row.employee_name}
                </td>
                <td colSpan={3} className="font-weight-bold">
                  {CurrencyService.formatCurrency(row.total_price_employee)}
                </td>
              </tr>
            ) : (
              <tr>
                <td colSpan={22} className="text-left font-weight-bold">
                  {row.employee_name}
                </td>
              </tr>
            )}

            {row.datas.map((item, id) => {
              return (
                <tr key={id}>
                  <td colSpan={2}>{item.row_num}</td>
                  <td colSpan={4}>{item.tekmedi_card_number}</td>
                  <td colSpan={4}>{item.patient_code}</td>
                  <td colSpan={3}>{item.patient_name}</td>
                  <td colSpan={3}>{item.time}</td>
                  <td colSpan={3}>{item.payment_type_name}</td>
                  <td colSpan={3}>
                    {CurrencyService.formatCurrency(item.price)}
                  </td>
                </tr>
              );
            })}
          </tbody>
        );
      });
    };

    const renderTFoot = () => {
      if (info.data_groups.length) {
        return (
          <tbody>
            <tr>
              <td colSpan={19} className="text-right font-weight-bold">
                Tổng tiền
              </td>
              <td colSpan={3} className="font-weight-bold">
                {CurrencyService.formatCurrency(info.total_price)}
              </td>
            </tr>
          </tbody>
        );
      }
    };

    const renderFooter = () => {
      return (
        <div
          style={{
            float: 'right',
            width: '450px',
            textAlign: 'center',
            marginTop: '50px',
          }}
        >
          <p>
            {' '}
            Bệnh viện Chợ Rẫy, {moment().format('HH')} giờ{' '}
            {moment().format('mm')} phút , Ngày {moment().format('DD')} tháng{' '}
            {moment().format('MM')} năm {moment().format('YYYY')}
          </p>
          <p style={{ marginTop: '50px' }}>
            {!_.isEmpty(user) ? user.full_name : ''}
          </p>
        </div>
      );
    };

    return (
      <div ref={ref} {...info}>
        {renderHeaDer()}
        {renderAdminView()}
        <table className="tablePdf">
          <thead>
            <tr className="tag-td-user">
              <td colSpan={2}>Số phiếu</td>
              <td colSpan={4}>Mã thẻ BN</td>
              <td colSpan={4}>Mã bệnh nhân</td>
              <td colSpan={3}>Tên bệnh nhân</td>
              <td colSpan={3}>Ngày giờ</td>
              <td colSpan={3}>Loại hình</td>
              <td colSpan={3}>Số tiền</td>
            </tr>
          </thead>
          {renderTable()}
          {renderTFoot()}
        </table>
        {renderFooter()}
      </div>
    );
  },
);
