/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState } from 'react';
import * as _ from 'lodash';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import { DialogKey } from 'store/patient/constants/http.constant';
import { makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton/IconButton';
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles({
  root: {
    paddingBottom: 0,
    '& h2': {
      fontSize: '1.3rem !important',
      fontWeight: 'bold',
    },
  },
});

interface ModalProps {
  title: string;
  open: boolean;
  onClose: () => void;
  confirmMethod: () => any;
  rejectMethod: any;
}

export const UpdateBalanceConfirmModal = ({
  title,
  open,
  onClose,
  confirmMethod,
  rejectMethod,
}: ModalProps) => {
  const classes = useStyles();

  return (
    <Dialog
      open={open}
      onClose={onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      disableBackdropClick={true}
    >
      <DialogTitle classes={classes}>{'Thông báo xác nhận'}</DialogTitle>
      <DialogContent>
        <div className="position-absolute top-0 right-0 p-2">
          <IconButton size="medium" onClick={onClose}>
            <CloseIcon />
          </IconButton>
        </div>
        <DialogContentText id="alert-dialog-description">
          {title}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={rejectMethod}
          color="secondary"
          classes={{ root: 'btn-default btn-danger' }}
        >
          Không
        </Button>
        <Button
          onClick={confirmMethod}
          color="primary"
          classes={{ root: 'btn-default btn-primary t-btn-primary' }}
        >
          Có
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default UpdateBalanceConfirmModal;
