import { HttpRequest } from 'utils/request';
import { APP_CONSTANT } from '../../common/constants/common.constant';
import { StatisticalPatientApiRoutes } from '../constants/statistical.constant';

export class StatisticaltHttp {
  request: any;
  constructor(endPoint = APP_CONSTANT.API.ENDPOINT) {
    this.request = new HttpRequest(endPoint).request;
  }

  public statisticalPatient = (data: any): Promise<any> => {
    return this.request.post(
      StatisticalPatientApiRoutes.STATISTICAL_PATIENT,
      data,
    );
  };

  public exportStatistical = (data: any): Promise<any> => {
    return this.request.post(
      StatisticalPatientApiRoutes.EXPORT_STATISTICAL,
      data,
    );
  };
}
