import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { TransactionHttp } from '../services/transaction.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';
import { APP_CONSTANT } from 'store/common/constants/common.constant';

const http = new TransactionHttp();
const paymentHttp = new TransactionHttp(APP_CONSTANT.API.WEB);

export function* getDataPaidWaiting(api, action) {
  const response = yield call(api.statisRequest);
  try {
    if (response.ok) {
      yield put(actions.getDataPaidWaitingSuccess(response.data));
    } else {
      yield put(actions.getDataPaidWaitingFailure(response.error));
    }
  } catch (err) {
    yield put(actions.getDataPaidWaitingFailure(err));
  }
}
export function* getListTranSaction(api, action) {
  const response = yield call(api.getListTranSaction, action.payload);
  try {
    if (response.ok) {
      yield put(actions.getListTranSactionSuccess(response.data));
    } else {
      yield put(actions.getListTranSactionFail(response.error));
    }
  } catch (error) {
    yield put(actions.getListTranSactionFail(error));
  }
}
export function* getListTransactionDetail(api, action) {
  const response = yield call(api.getListTransactionDetail, action.payload);
  try {
    if (response.ok) {
      yield put(actions.getListTransactionDetaiSuccess(response.data));
    } else {
      yield put(actions.getListTransactionDetaiFail(response.error));
    }
  } catch (error) {
    yield put(actions.getListTransactionDetaiFail(error));
  }
}
export function* getListTransactionDetailModal(api, action) {
  const response = yield call(
    api.getListTransactionDetailModal,
    action.payload,
  );
  try {
    if (response.ok) {
      yield put(actions.getListTransactionDetailModalSuccess(response.data));
    } else {
      yield put(actions.getListTransactionDetailModalFail(response.error));
    }
  } catch (error) {
    yield put(actions.getListTransactionDetailModalFail(error));
  }
}

export function* getMedicineReport(api, action) {
  const response = yield call(api.getMedicineReport, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.getMedicineReportSuccess(data));
  } catch (error) {
    yield put(actions.getMedicineReportFail(_.get(error, 'message')));
  }
}

export function* exportMedicine(api, action) {
  const response = yield call(api.exportMedicine, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.exportMedicineSuccess(data));
  } catch (error) {
    yield put(actions.exportMedicineFail(_.get(error, 'message')));
  }
}

export function* cancelTransaction(api, action) {
  const response = yield call(api.cancelTransaction, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.cancelTransactionSuccess(data));
  } catch (error) {
    yield put(
      actions.cancelTransactionFail(_.get(error, 'message') || 'Huỷ thất bại'),
    );
  }
}

export function* PaidWaitingFormSaga() {
  yield all([
    yield takeLatest(actions.getDataPaidWaiting.type, getDataPaidWaiting, http),
    yield takeLatest(actions.exportMedicine.type, exportMedicine, http),
    yield takeLatest(actions.getMedicineReport.type, getMedicineReport, http),
    yield takeLatest(actions.getListTranSaction.type, getListTranSaction, http),
    yield takeLatest(
      actions.getListTransactionDetail.type,
      getListTransactionDetail,
      http,
    ),
    yield takeLatest(
      actions.getListTransactionDetailModal.type,
      getListTransactionDetailModal,
      http,
    ),
    yield takeLatest(
      actions.cancelTransaction.type,
      cancelTransaction,
      paymentHttp,
    ),
  ]);
}
