export function* CardFormSaga() {
  yield all([
    yield takeLatest(
      actions.getDataCardHistory.type,
      getDataCardHistory,
      cardRequest,
    ),
    yield takeLatest(
      actions.getCardStatistical.type,
      getDataCardStatistical,
      cardRequest,
    ),
    yield takeLatest(
      actions.registerCardPayment.type,
      registerCardPayment,
      cardRequest,
    ),
    yield takeLatest(actions.exportListCard.type, exportListCard, cardRequest),
    yield takeLatest(actions.cancelPayment.type, cancelPayment, paymentRequest),
    yield takeLatest(actions.getIdPrinter.type, getIdPrinter, cardRequest),
    yield takeLatest(actions.postCancelDeal.type, postCancelDeal, cardRequest),
    yield takeLatest(
      actions.getListValueExtend.type,
      getListValueExtend,
      cardRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCard.type,
      exportHistoryCard,
      cardRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCardPdf.type,
      exportHistoryCardPdf,
      cardRequest,
    ),
    yield takeLatest(
      actions.exportHistoryCardPdfOver.type,
      exportHistoryCardPdfOver,
      cardRequest,
    ),
    yield takeLatest(
      actions.exportTransactionCard.type,
      exportTransactionCard,
      cardRequest,
    ),
  ]);
}
