import { call, put, takeLatest, all } from 'redux-saga/effects';
import { actions } from './slice';
import { StatisticaltHttp } from '../services/statistical.http';
import { configResponse } from 'utils/request';
import * as _ from 'lodash';

const configRequest = new StatisticaltHttp();

export function* statisticalPatient(api, action) {
  const response = yield call(api.statisticalPatient, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.statisticalPatientSuccess(data));
  } catch (error) {
    yield put(actions.statisticalPatientFailure(_.get(error, 'message')));
  }
}

export function* exportStatistical(api, action) {
  const response = yield call(api.exportStatistical, action.payload);
  try {
    const data = yield configResponse(response);
    yield put(actions.exportStatisticalSuccess(data));
  } catch (error) {
    yield put(actions.exportStatisticalFailure(_.get(error, 'message')));
  }
}

export function* StatisticalSaga() {
  yield all([
    yield takeLatest(
      actions.statisticalPatient.type,
      statisticalPatient,
      configRequest,
    ),
    yield takeLatest(
      actions.exportStatistical.type,
      exportStatistical,
      configRequest,
    ),
  ]);
}
